.about {
  height: 100vh;
  width: 76vw;
  background-color: grey;
  align-self: center;
  margin: 5px 5px 0 5px;
}

#about {
  display: flex;
}

#me {
  width: 30%;
  height: 30%;
}

#name {
  color: white;
  font-size: 40px;
  display: flex;
  justify-content: center;
  width: 70%;
  align-items: center;
  background-color: rgb(90, 82, 82);
}

#life {
  width: 100%;
  background-color: grey;
  color: white;
}

#skills {
  width: 100%;
  background-color: grey;
  color: white;
}

@media only screen and (max-width: 1100px) {
 
  .about{
    width:100vw;
  }
}
